import React, { useState } from 'react'
import { Form, Input, Button } from 'antd'
import { customAxiosApi } from '../reuse/CustomAxios'
import { REGEX, API_NAME } from '../dataConst'
import { useHistory } from 'react-router-dom'
import { useDispatch } from 'react-redux'
import { setStatusLogin } from '../redux/statusLoginSlice'
import './style.scss'

const ForgotPassword = () => {
  const history = useHistory()
  const dispatch = useDispatch()
  const [dataCode, setDateCode] = useState(null)
  const [validEmail, setValidEmail] = useState('')
  const [validCode, setValidCode] = useState('')
  const [isBoxChangePassword, setIsBoxChangePassword] = useState(null)

  const layout = {
    labelCol: {
      span: 6,
    },
    wrapperCol: {
      span: 16,
    },
  }
  const tailLayout = {
    wrapperCol: {
      offset: 6,
      span: 16,
    },
  }

  const validatorEmail = (rules, value) => {
    const regex = REGEX.EMAIL
    const match = regex.test(value)

    if (!value) return

    if (validEmail) {
      return Promise.reject(validEmail)
    }

    if (!match) {
      return Promise.reject('Email khng hp l !')
    }

    return Promise.resolve()
  }

  const validatorPassWord = (rules, value) => {
    const regex = REGEX.PASSWORD
    const match = regex.test(value)

    if (!value) return

    if (!match) {
      return Promise.reject('Ti thiu 8 k t, t nht mt ch ci v mt s!')
    }

    return Promise.resolve()
  }

  const onFinish = (values) => {
    setValidEmail('')
    customAxiosApi.post('auth/forgotPassword', values).then((res) => {
      const newData = res.data

      if (typeof newData === 'string') {
        setValidEmail(newData)
        return
      }

      if (typeof newData === 'object') {
        setDateCode(newData)
      }
    })
  }

  const handleSubmitCode = (values) => {
    setValidCode('')
    if (values.code === dataCode.code) {
      setIsBoxChangePassword(true)
      return
    }
    setValidCode('Bn nhp sai m Code!')
  }

  const handleSubmitChangePassword = (values) => {
    customAxiosApi
      .put(`${API_NAME.USERS}/${dataCode.id}`, { password: values.password })
      .then(() => {
        history.push('/')
        dispatch(setStatusLogin(true))
      })
  }

  return (
    <>
      <div className="forgot-password">
        <div className="container">
          <div className="row justify-center">
            <div className="col-xl-6">
              {isBoxChangePassword ? (
                <div className="forgot-password__box">
                  <h3 className="forgot-password__title">Nhp mt khu mi:</h3>

                  <p className="forgot-password__text">
                    Vui lng nhp email hoc s in thoi  tm kim ti khon
                    ca bn. To mt khu c t nht 8 k t. Mt khu mnh l
                    s kt hp ca cc ch ci v s.
                  </p>

                  <Form
                    name="change-password"
                    {...layout}
                    initialValues={{
                      remember: true,
                    }}
                    onFinish={handleSubmitChangePassword}
                  >
                    <Form.Item
                      name="password"
                      label="Password"
                      rules={[
                        {
                          validator: validatorPassWord,
                        },
                        {
                          required: true,
                          message: 'Hy nhp mt khu !',
                        },
                      ]}
                      hasFeedback
                    >
                      <Input.Password />
                    </Form.Item>

                    <Form.Item
                      name="confirm"
                      label="Confirm Password"
                      dependencies={['password']}
                      hasFeedback
                      rules={[
                        {
                          required: true,
                          message: 'Hy nhp li mt khu !',
                        },
                        ({ getFieldValue }) => ({
                          validator(_, value) {
                            if (!value || getFieldValue('password') === value) {
                              return Promise.resolve()
                            }

                            return Promise.reject(
                              'Nhp li mt khu khng hp l !',
                            )
                          },
                        }),
                      ]}
                    >
                      <Input.Password />
                    </Form.Item>

                    <Form.Item {...tailLayout}>
                      <Button type="primary" htmlType="submit">
                        Submit
                      </Button>
                    </Form.Item>
                  </Form>
                </div>
              ) : !dataCode ? (
                <div className="forgot-password__box">
                  <h3 className="forgot-password__title">
                    Tm kim ti khon:
                  </h3>

                  <p className="forgot-password__text">
                    Vui lng nhp email  tm kim ti khon ca bn.
                  </p>

                  <Form
                    name="submit-code"
                    initialValues={{
                      remember: true,
                    }}
                    onFinish={onFinish}
                  >
                    <Form.Item
                      label="Email"
                      name="email"
                      rules={[
                        {
                          required: true,
                          message: 'Vui lng nhp email!',
                        },
                        {
                          validator: validatorEmail,
                        },
                      ]}
                      extra={validEmail}
                    >
                      <Input onChange={() => setValidEmail('')} />
                    </Form.Item>
                    <span
                      className="forgot-password__icon fas fa-paper-plane"
                      onClick={onFinish}
                    />
                  </Form>
                </div>
              ) : (
                <div className="forgot-password__box">
                  <h3 className="forgot-password__title">Nhp m Code:</h3>
                  <p className="forgot-password__text">
                    Bn  nhn c mt tin nhn vn bn c m code trong mail
                    ca mnh. M ny cha 6 k t.
                  </p>

                  <Form
                    initialValues={{
                      remember: true,
                    }}
                    onFinish={handleSubmitCode}
                  >
                    <Form.Item
                      label="Code"
                      name="code"
                      extra={validCode}
                      rules={[
                        {
                          required: true,
                          message: 'Vui lng nhp m code',
                        },
                      ]}
                    >
                      <Input onChange={() => setValidCode('')} />
                    </Form.Item>
                    <span
                      className="forgot-password__icon fas fa-paper-plane"
                      onClick={handleSubmitCode}
                    />
                  </Form>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default ForgotPassword
