import React, { useState, useEffect } from 'react'
import { Form, Input, Select, Button } from 'antd'
import { customAxiosApi } from '../reuse/CustomAxios'
import { REGEX, API_NAME } from '../dataConst'
import { useDispatch, useSelector } from 'react-redux'
import { fetchUser } from '../redux/loginSlice'
// import { fetchGender } from '../redux/genderSlice'
import './infoUser.scss'

const InfoUser = ({ dataOrder }) => {
  const dispatch = useDispatch()
  const [form] = Form.useForm()
  const [isDisabled, setIsDisable] = useState(true)
  const [infoUser, setInfoUser] = useState({})
  const dataGender = useSelector((state) => state.gender.list)
  const [validEmail, setValidEmail] = useState('')

  useEffect(() => {
    const id = sessionStorage.getItem('id')
    dispatch(fetchUser(id)).then((res) => {
      setInfoUser(res.payload)
    })
  }, [dispatch])

  const validatorEmail = (rules, value) => {
    const regex = REGEX.EMAIL
    const match = regex.test(value)

    if (validEmail) {
      return Promise.reject(validEmail)
    }

    if (!match) {
      return Promise.reject('Email khng hp l !')
    }

    return Promise.resolve()
  }

  const validatorPhone = (rules, value) => {
    const regex = REGEX.PHONE
    const match = regex.test(value)

    if (!match) {
      return Promise.reject('u s 09|03|08|05|07 gm 10 s !')
    }

    return Promise.resolve()
  }

  const validatorPassWord = (rules, value) => {
    const regex = REGEX.PASSWORD
    const match = regex.test(value)

    if (!match) {
      return Promise.reject('Ti thiu 8 k t, t nht mt ch ci v mt s!')
    }

    return Promise.resolve()
  }

  const layout = {
    labelCol: { span: 4 },
    wrapperCol: { span: 16 },
  }
  const tailLayout = {
    wrapperCol: { offset: 4, span: 16 },
  }

  const onFinish = (values) => {
    const id = sessionStorage.getItem('id')
    customAxiosApi.put(`${API_NAME.USERS}/${id}`, values).then((resp) => {
      const { data } = resp

      if (typeof data === 'string') {
        setValidEmail('Email  tn ti !')
        return
      }
      dispatch(fetchUser(id)).then((res) => {
        setInfoUser(res.payload)
      })
      setIsDisable(true)
    })
  }

  return (
    <>
      <div className="box-title">
        <h6 className="title-info">Thng tin ti khon</h6>
        <p className="btn-edit-user" onClick={() => setIsDisable(false)}>
          <span className="btn-edit-user--icon fas fa-edit" />
          Cp nht thng tin ti khon
        </p>
      </div>

      {infoUser.name && (
        <Form form={form} name="register" onFinish={onFinish} {...layout}>
          <Form.Item
            name="name"
            label="H v tn"
            initialValue={infoUser.name}
            rules={[
              {
                required: true,
                message: 'Hy nhp User Name !',
                whitespace: true,
              },
            ]}
          >
            <Input disabled={isDisabled} />
          </Form.Item>

          <Form.Item
            name="email"
            label="Email"
            initialValue={infoUser.email}
            rules={[
              {
                validator: validatorEmail,
              },
              {
                required: true,
                message: 'Hy nhp Email !',
              },
            ]}
          >
            <Input disabled={isDisabled} />
          </Form.Item>

          <Form.Item
            name="genderID"
            label="Gii tnh"
            initialValue={infoUser.genderID}
            rules={[
              {
                required: true,
                message: 'Hy nhp gii tnh !',
              },
            ]}
          >
            <Select disabled={isDisabled}>
              {dataGender.map((item) => (
                <Select.Option value={item.id} key={item.id}>
                  {item.name}
                </Select.Option>
              ))}
            </Select>
          </Form.Item>

          <Form.Item
            name="phone"
            label="S in thoi"
            initialValue={infoUser.phone}
            rules={[
              {
                validator: validatorPhone,
              },
              {
                required: true,
                message: 'Hy nhp s in thoi !',
              },
            ]}
          >
            <Input
              disabled={isDisabled}
              style={{
                width: '100%',
              }}
            />
          </Form.Item>

          <Form.Item
            name="address"
            label="i ch"
            initialValue={infoUser.address}
            rules={[
              {
                required: true,
                message: 'Hy nhp a ch !',
                whitespace: true,
              },
            ]}
          >
            <Input disabled={isDisabled} />
          </Form.Item>

          {!isDisabled && (
            <>
              <Form.Item
                name="password"
                label="Password"
                rules={[
                  {
                    validator: validatorPassWord,
                  },
                  {
                    required: true,
                    message: 'Hy nhp mt khu !',
                  },
                ]}
                hasFeedback
              >
                <Input.Password />
              </Form.Item>

              <Form.Item
                name="confirm"
                label="Confirm Password"
                dependencies={['password']}
                hasFeedback
                rules={[
                  {
                    required: true,
                    message: 'Hy nhp li mt khu !',
                  },
                  ({ getFieldValue }) => ({
                    validator(_, value) {
                      if (!value || getFieldValue('password') === value) {
                        return Promise.resolve()
                      }

                      return Promise.reject('Nhp li mt khu khng hp l !')
                    },
                  }),
                ]}
              >
                <Input.Password />
              </Form.Item>

              <Form.Item {...tailLayout}>
                <Button type="primary" htmlType="submit">
                  Lu thay i
                </Button>

                <Button
                  type="primary"
                  danger
                  onClick={() => setIsDisable(true)}
                >
                  Hy b
                </Button>
              </Form.Item>
            </>
          )}
        </Form>
      )}

      <div className="box-title">
        <h6 className="title-info">S lng n hng bn  mua</h6>
      </div>

      {dataOrder.length ? (
        <p className="info-user__text">
          {' '}
          Bn  mua {dataOrder.length} n hng.{' '}
        </p>
      ) : (
        <p className="info-user__text"> Bn cha t mua sn phm. </p>
      )}
    </>
  )
}

export default InfoUser
